* {
  font-family: 'Roboto', sans-serif; }

header {
  position: relative; }

nav {
  height: 50px;
  background-color: #212121; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  .menu_link {
    color: #FFFFFF;
    font-size: 15px;
    font-weight: 300; }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 28px; } }

.subheader {
  position: absolute;
  width: 100%;
  height: 84px;

  /* opacity: 0.60; якщо використовувати цей методЮ то все в блоці буде прозоре */
  background-color: rgba(0, 0, 0, 0.6);

  /* з таким підходом прозорим буде тільки фон */

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_official {
    margin-top: 6px;
    opacity: 0.5;
    font-size: 13px;
    font-weight: 400;
    color: #FFFFFF; }

  .subheader_call {
    color: #FFFFFF;
    font-size: 12px;
    font-weight: 300;
    margin-top: 19px;
    text-align: right; }

  .subheader_phone {
    display: block;
    margin-top: 8px;
    color: #FFFFFF;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    line-height: 12px; }

  .subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    color: #FFFFFF; } }

.promo {
  min-height: 800px;
  background: url('../img/bg/main_bg.png') center (center / cover) no-repeat;
  padding: 150px 0 135px 0;

  .promo_header {
    font-size: 60px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    color: #FFFFFF;
    line-height: 60px;
    margin: 0;
    margin-top: 22px; }

  .promo_subheader {
    font-size: 36px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    color: #FFFFFF;
    line-height: 36px;
    margin: 0;
    margin-top: 22px; }

  .promo_descr {
    font-size: 18px;
    font-weight: 400;
    font-style: normal;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF;
    margin-top: 23px; }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #FFFFFF;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase; } }

.reasons {
  padding: 69px 0 77px 0; }

.label {
  margin: 0 auto;
  padding: 5px 12px;
  width: 115px;
  height: 27px;
  background: rgba(0, 0, 0, 1);
  font-size: 11px;
  font-weight: 300;
  text-align: center;
  color: #FFFFFF; }

h2.title {
  margin-top: 37px;
  font-size: 38px;
  font-weight: 400;
  text-align: center;
  color: #222222; }

.reasons .reasons_block {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 35px;

  .reasons_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    border-radius: 100%;
    width: 116px;
    height: 116px;
    background: rgba(30, 172, 199, 1); }

  .reasons_descr {
    width: 342px;

    .reasons_subtitle {
      font-size: 17px;
      font-weight: 400;
      text-align: justify;
      color: #1EACC7; }

    .reasons_text {
      color: #202020;
      font-size: 15px;
      font-weight: 300;
      line-height: 21px; } } }

